.section-settings .body {
    padding: 20px;
}
.section-settings .body .item {
    background: #fff;
    border-radius: 3px;
    margin-bottom: 15px;
}
.section-settings .body .item:after {
    content: '';
    display: block;
    clear: both;
}
.section-settings .body .item > div {
    padding: 10px 15px;
    float: left;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.section-settings .body .item .title {
    font-weight: bold;
    line-height: 17px;
    width: 60%;
    color: #0099FD;
    font-size: 14px;
    cursor: pointer;
}
.section-settings .body .item .title span {
    border-bottom: 1px dashed #0099FD;
}
.section-settings .body .item .title:hover {
    color: #000;
}
.section-settings .body .item .title:hover span {
    border-color: #000;
}
.section-settings .body .item .caption {
    font-size: 12px;
    line-height:17px;
    color: #fff;
    text-align: center;
    background: #eb0000;
    width: 40%;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
}
.section-settings .body .item .caption.bought {
    background: #0099FD;
}
.section-settings .body .item .caption.activate {
    cursor: pointer;

}
.section-settings .body .item .caption.activate:hover {
    background: #ff4c3d;
}
.section-settings .body .item .description {
    width: 100%;
    display: none;
}

.section-settings .body .item.opened .description {
    display: block;
}
.section-settings .body .item.opened .title {
    color: #999;
}
.section-settings .body .item.opened .title span {
    border-bottom-color: #999;
}

.section-settings .body .description p {
    margin-top: 0;
}
.section-settings .body .description p span {
    font-size: 15px;
}

.section-settings .body .description ul {
    padding: 10px 0 5px 0;
}
.section-settings .body .description li {
    font-weight: bold;
    list-style: none;
    background: url(../img/check.png) no-repeat 0 2px;
    padding-left: 35px;
    padding-bottom: 7px;
}



