body {
  margin: 0;
  font-size: 12px;
  cursor: default;
}

body::after {
  content: "";
  opacity: 0.6;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: url(../img/bg-light.png);
}

#root {
  border: 1px solid gray;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body,
textarea {
  font-family: Raleway, sans-serif;
}

html,
body,
#root {
  height: 100%;
}

