.normal-font {
  font-family: Verdana, sans-serif;
}

.pageWrapper {
  width: 100%;
  background: #ccc;
}

.block {
  text-align: center;
}

.content {
  height: calc(100vh - 47px);
}

.content > div {
  height: 100px;
}

.icon i {
  width: 15px;
  height: 15px;
  display: inline-block;
  background: url(../img/sprite.png);
  margin-right: 7px;
}

.pageWrapper {
  height: 100%;
}

.menu {
  background: #fff;
  height: 60px;
}
.menu a {
  display: block;
  float: left;
  width: 33.3%;
  height: 60px;
  background: no-repeat center center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 5px solid #f3f6f8;
}
.menu a.active {
  border-bottom: 5px solid #dd1f2d;
}
.menu a.m-chat {
  background-image: url(../img/menu/chat.png);
}
.menu a.m-call {
  background-image: url(../img/menu/phone.png);
}
.menu a.m-settings {
  background-image: url(../img/menu/settings.png);
}
body,
input,
select,
textarea {
  font-family: "Open Sans", Arial, Helvetica, sans-serif;
}

.screen {
  height: 100%;
}

.text button,
.text rButton {
  display: inline-block;
  border: 0;
  cursor: pointer;
  background: #dd1f2d;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  border-radius: 20px;
  color: #fff;
}

.text button.blue {
  background: #555;
}

.header {
  background: #007edf;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 15px 20px;
}
.header > div {
  vertical-align: middle;
  display: inline-block;
}
.header > .hm {
  background: url(../img/header/hm.png) no-repeat;
  background-size: contain;
  width: 120px;
  height: 35px;
}
.header > .lifeline {
  background: url(../img/header/lifeline.png) no-repeat;
  background-size: contain;
  width: 50px;
  height: 50px;
  margin: 0 10px;
}

.header > .text {
  width: 140px;
  color: #fff;
  font-size: 12px;
}



