.chatHeader {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 40px;
  background: #fff;
  border-bottom: 1px solid #ededed;
}
.chatHeader > .title {
  padding: 12px 20px;
  font-size: 14px;
}
.chatHeader .button {
  padding: 12px 20px;
  font-weight: bold;
}
.chatMenu {
  position: absolute;
  top: 0;
  right: 0;
}
.chatMenu .button {
  display: inline-block;
}
